<template>
    <view class="sj-list-item" @tap="onClick">
        <view class="sj-list-item_thumb">
            <image class="thumb-img" :style="thumbStyle" src="../../static/logo.png" v-if="thumb"></image>
            <slot name="thumb"></slot>
        </view>
        <view class="sj-list-item_info">
            <view class="info" :style="{height: thumbStyle.height}" v-if="info.title">
                <view class="info-title">{{info.title}}</view>
                <view class="info-desc" v-if="info.desc">{{info.desc}}</view>
            </view>
            <slot name="info"></slot>
        </view>
        <view class="sj-list-item_icon">
            <uni-icons v-if="rightIcon" type="right" size="16" color="#808080"></uni-icons>
            <slot name="icon"></slot>
        </view>
    </view>
</template>

<script>
    export default {
        name:"sj-list-item",
        computed: {
            thumbStyle() {
                const type = {
                    base: '120rpx',
                    lg: '240rpx',
                    sm: '80rpx'
                }
                return {
                    width: type[this.thumbType],
                    height: type[this.thumbType]
                }
            }
        },
        props: {
            // 左侧缩略图
            thumb: {
                type: String,
                default: ''
            },
            
            // 缩略图大小
            thumbType: {
                type: String,
                default: 'base'
            },
            
            // 中间主体内容 
            info: {
                type: Object,
                default() {
                    return {
                        title: ''
                    }
                }
            },
            
            // 右侧箭头
            rightIcon: {
                type: Boolean,
                default: true
            }
        },
        
        methods: {
            onClick() {
                this.$emit('on-click')
            }
        }
    }
</script>

<style lang="less" scoped>
.sj-list-item{
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    padding: 20rpx;
    border-bottom: 1px solid #d8d8d8;
    
    &_thumb{
        .thumb-img{
            vertical-align: middle;
        }
    }
    
    &_info{
        flex-grow: 1;
        padding: 0 20rpx;
        
        .info{
            display: flex;
            flex-direction: column;
            justify-content: space-around;
            
            .info-title{
                font-size: 32rpx;
            }
            
            .info-desc{
                font-size: 28rpx;
                color: #808080;
            }
        }
    }
}
</style>
